import style from "../../PlateformUser/PlateformUser.module.css";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import { useEffect, useState } from "react";
import ClassInternalBatchStudentCard from "./ClassInternalBatchStudentCard";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useGetClassBatchAllStudent } from "../../../../hooks/member_tab/class-api";
const ClassInternalBatchStudent = ({
  title,
  onClose,
  selectedId,
  onSelectStudent,
  isMultiSelect,
  onMultiSelect,
  selectList,
  onDone,
  batchId,
  onSelectAll,
  onRemoveAll,
  buttonTitle,
}) => {
  const { t } = useTranslation();
  const [studentList, setStudentList] = useState([]);
  const [search, setSearch] = useState("");
  const [isAllSelected, setIsAllSelected] = useState(false);
  const { getClassBatchAllStudent, getClassBatchAllStudentLoading } =
    useGetClassBatchAllStudent({
      data: {
        bid: batchId,
        page: 1,
        limit: 1000,
        search: "",
      },
      skip: !batchId,
    });
  useEffect(() => {
    if (getClassBatchAllStudent?.all_students?.length) {
      setStudentList(getClassBatchAllStudent?.all_students);
    }
  }, [getClassBatchAllStudent?.all_students]);

  const onSelect = (value) => {
    if (selectedId === value?._id) {
    } else {
      onSelectStudent(value);
    }
  };
  const select = () => {
    setIsAllSelected(true);
    let arr = [];
    for (let dt of getClassBatchAllStudent?.all_students) {
      arr.push(dt?._id);
    }
    onSelectAll(arr);
  };
  const remove = () => {
    setIsAllSelected(false);
    onRemoveAll();
  };
  const onSearchEvent = (e) => {
    if (
      !getClassBatchAllStudentLoading &&
      getClassBatchAllStudent?.all_students?.length >= 0
    ) {
      setSearch(e.target.value);
    }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      let ft = getStudentFilter(getClassBatchAllStudent?.all_students, search);
      setStudentList(ft);
    }
  };

  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{title ?? t("student_list")}</h6>
            </div>
            <div className={style.modal_container_btn_container}>
              {isMultiSelect && (
                <CreateButton
                  label={t(buttonTitle ?? "selected")}
                  count={selectList?.length}
                  onAction={onDone}
                  customStyle={{
                    margin: "0",
                    padding: "0.3rem 0.5rem",
                  }}
                />
              )}
              {isAllSelected ? (
                <CreateButton
                  label={t("remove_all")}
                  onAction={remove}
                  customStyle={{
                    margin: "0",
                    padding: "0.3rem 0.5rem",
                  }}
                />
              ) : (
                <CreateButton
                  label={t("select_all")}
                  onAction={select}
                  customStyle={{
                    margin: "0",
                    padding: "0.3rem 0.5rem",
                  }}
                />
              )}

              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingBottom: "1rem",
          }}
        >
          <div
            className={style.assign_search_container}
            style={{
              marginBottom: "1rem",
            }}
          >
            <input
              type="text"
              placeholder={t("search")}
              className={style.assign_search_input}
              onChange={onSearchEvent}
              onKeyUp={onCallBindSearch}
              style={{
                width: "100%",
              }}
            />
            <img
              src={`${assestsNavbarUrl}/navbar-search.svg`}
              alt="search icon"
            />
          </div>

          {studentList?.map((student) => (
            <ClassInternalBatchStudentCard
              student={student}
              key={student?._id}
              selectedId={selectedId}
              onSelect={onSelect}
              selectList={selectList}
              isMultiSelect={isMultiSelect}
              onMultiSelect={onMultiSelect}
            />
          ))}
          {getClassBatchAllStudentLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            studentList?.length === 0 && (
              <EmptyMessage title="No students found related this search." />
            )
          ) : (
            getClassBatchAllStudent?.all_students?.length === 0 && (
              <EmptyMessage title="No students." />
            )
          )}
        </UserMemberContentWrapper>
      </div>
    </Modal>
  );
};

export default ClassInternalBatchStudent;

const getStudentFilter = (list, args) => {
  if (list?.length > 0) {
    return list?.filter((val) => {
      let name = `${val?.studentFirstName ?? ""}${
        val?.studentMiddleName ? `${val?.studentMiddleName} ` : ""
      } ${val?.studentLastName ?? ""}`;
      if (name?.toLowerCase()?.includes(args?.toLowerCase())) {
        return val;
      } else {
        return null;
      }
    });
  } else {
    return [];
  }
};
